<script lang="ts">import { createEventDispatcher } from 'svelte';
;
import { colorMap, tileClick$ } from './store';
const dispatch = createEventDispatcher();
export let x;
export let y;
let value;
let revealed = false;
let mark;
let isMine = false;
let error = false;
export let disabled = false;
let onReveal;
let onMark;
export function getX() {
    return x;
}
export function getY() {
    return y;
}
export function getValue() {
    return value;
}
export function getRevealed() {
    return revealed;
}
export function getMark() {
    return mark;
}
export function getIsMine() {
    return isMine;
}
export function getError() {
    return error;
}
export function setValue(v) {
    value = v;
}
export function setRevealed(r) {
    revealed = r;
}
export function setMark(m) {
    mark = m;
}
export function setIsMine(m) {
    isMine = m;
}
export function setError(e) {
    error = e;
}
export function registerOnReveal(callback) {
    onReveal = (e) => {
        e.preventDefault();
        callback();
    };
}
export function registerOnMark(callback) {
    onMark = (e) => {
        e.preventDefault();
        callback();
    };
}
</script>

<style>
.tile {
	height: 40px;
	width: 40px;
	display: block;
	box-sizing: border-box;
}

div {
	user-select: none;
	font-family: 'Press Start 2P', cursive;
	border-style: solid;
	border-color: #a6a6a6;
	border-width: 1px;
	text-align: center;
	font-size: 32px;
	line-height: 42px;
}

.fontpatch {
	padding-left: 4px;
}

button {
	font-size: 32px;
	text-align: center;
	padding: 0;
}

.error {
	background-color: red;
}

img {
	image-rendering: pixelated;
	width: inherit;
	height: inherit;
	margin-top: -1px;
	margin-left: -1px;
}
</style>

{#if revealed}
	<div
		class="tile"
		class:fontpatch={!isMine && value && !error}
		class:error={error && isMine}
		style="color: {colorMap[value]}; grid-row: {x + 1}; grid-column: {y + 1};">
		{#if isMine}
			<img aria-label="mine" src="./assets/minesweeper/mine-small.png" alt="mine" />
		{:else if value}
			{#if error}
				<img aria-label="mine" src="./assets/minesweeper/mine-false-small.png" alt="mine" />
			{:else}{value}{/if}
		{/if}
	</div>
{:else}
	<button
		{disabled}
		class="tile button"
		class:error
		style="grid-row: {x + 1}; grid-column: {y + 1};"
		aria-label="Tile {mark}"
		on:click={onReveal}
		on:mousedown={() => tileClick$.next()}
		on:contextmenu={onMark}>
		{#if mark === 'flag'}
			<img aria-label="flag" src="./assets/minesweeper/flag-small.png" alt="flag" />
		{:else if mark === 'questionMark'}?{/if}
	</button>
{/if}
